.app {
  background: url("dark chatbot.bcfc240b.jpg") 0 0 / 100% 800px no-repeat;
  height: 715px;
}

.app > .screen {
  display: none;
}

img.app-icons {
  width: 5%;
  position: absolute;
  left: 30px;
}

.app-Title {
  text-align: center;
  color: #fff;
  -webkit-text-fill-color: transparent;
  background: -webkit-linear-gradient(#51baff, #e42999);
  -webkit-background-clip: text;
  padding: 12px 30px;
  font-size: 72px;
  font-weight: 600;
}

span.chat.title-text {
  -webkit-text-fill-color: transparent;
  background: -webkit-linear-gradient(#51baff, #e42999);
  -webkit-background-clip: text;
  position: absolute;
  top: 94px;
  left: 82%;
}

.app > .screen.active {
  display: block;
}

.screen .form {
  width: 80%;
  max-width: 400px;
  position: absolute;
  top: 45%;
  left: 30%;
  transform: translate(-50%, -50%);
}

.screen .form-input {
  width: 100%;
  margin: 20px 0;
}

.screen h2 {
  margin-bottom: 25x;
  color: #fff;
  border-bottom: 4px solid #555;
  padding: 5px 0;
  font-size: 60px;
  display: inline-block;
}

.screen .form-input label {
  color: #fff;
  margin-bottom: 15px;
  font-size: x-large;
  font-weight: 800;
  display: block;
}

.screen .form-input input {
  color: #fff;
  background: #000;
  border: 2px solid #51baff;
  border-radius: 12px;
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

.screen .form-input button {
  color: #eee;
  cursor: pointer;
  background: green;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 7px 27px;
  font-size: 20px;
  font-weight: 800;
}

.screen .form-input button:hover {
  color: #000;
}

.messages {
  max-height: 400px;
  overflow-y: scroll;
}

.screen.chat-screen.active {
  width: 50%;
  display: block;
  position: absolute;
  left: 25%;
}

.chat-screen .header {
  background: #111;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  width: 93%;
  height: 50px;
  padding: 0 20px;
  display: flex;
}

.chat-screen .header .logo {
  color: #eee;
  font-size: 40px;
  font-weight: 600;
}

.chat-screen .header button {
  color: #eee;
  cursor: pointer;
  background: #c31818;
  border-radius: 15px;
  outline: none;
  padding: 8px 20px;
  font-size: 16px;
  font-weight: 800;
}

.chat-screen .header button:hover {
  background: #8b0000;
}

.chat-screen .messages {
  background: url("cartoon-dark-black.68985a07.jpg") 0 0 / 100% 100% no-repeat;
  border-radius: 18px;
  width: 100%;
  height: calc(100% - 100px);
  overflow: auto;
}

.chat-screen .messages .message {
  background-size: 100%;
  padding: 10px;
  display: flex;
}

.chat-screen .messages .message > div {
  box-sizing: 0px 0px 20px 5px #0000000d;
  background: #f7f7f7;
  height: 40px;
  padding: 10px;
}

.chat-screen .messages .message .my-message {
  background: #111;
  justify-content: flex-end;
}

.chat-screen .messages .message .other-message {
  justify-content: flex-start;
}

.messages.my-message {
  text-align: end;
}

.my-message.text, .other-message.text {
  background: #fff;
  display: inline-block;
}

.chat-screen .messages .message .name {
  color: #555;
  margin-bottom: 5px;
  font-size: 13px;
}

.chat-screen .messages .message .text {
  word-wrap: break-word;
}

.chat-screen .messages .update {
  text-align: center;
  color: #fff;
  background: #35353566;
  padding: 10px;
  font-style: italic;
}

.chat-screen .typebox {
  width: 100%;
  height: 50px;
  display: flex;
}

.chat-screen .typebox input {
  border-radius: 18px;
  flex: 1;
  font-size: 17px;
}

.chat-screen .typebox button {
  color: #eee;
  cursor: pointer;
  background: green;
  border: none;
  border-radius: 15px;
  outline: none;
  width: 80px;
  height: 100%;
  margin-left: 8px;
  font-size: 16px;
  font-weight: bolder;
}

.chat-screen .typebox button:hover {
  background: #006400;
}

.message.other-message {
  justify-content: end;
}

img.MW.logo {
  width: 30%;
  position: absolute;
  top: 75px;
}

.messages {
  padding-bottom: 80px;
}

@media only screen and (width >= 1708px) {
  .chat-screen .messages {
    background: url("cartoon-dark-black.68985a07.jpg") 0 0 / 100% 100% no-repeat;
    border-radius: 18px;
    width: 94%;
    height: calc(100% - 100px);
    overflow: auto;
  }

  .chat-screen .typebox {
    width: 94%;
    height: 50px;
    display: flex;
  }

  .chat-screen .header {
    width: 90%;
  }
}
